import { CERTS } from '../app/shared/CERTS';
import { ImageList, ImageListItem } from '@mui/material';

export default function EducationSection() {
  const certs = CERTS;

  return (
    <ImageList sx={{ maxWidth: 500, height: 450 }} cols={4} rowHeight={164}>
      {certs.map((cert) => (
        <ImageListItem key={cert.img}>
          <img
            srcSet={`${cert.img}?w=164&h=164&fit=crop&auto=format&dpr=2 2x`}
            src={`${cert.img}?w=164&h=164&fit=crop&auto=format`}
            alt={cert.title}
            loading="lazy"
          />
        </ImageListItem>
      ))}
    </ImageList>
  );
}
